import Vue from 'vue'
import vuex from 'vuex'
Vue.use(vuex)
const moudleA = {
  state: {
  },
  getters: {
  },
  mutations: {},
  actions: {}
}
const moudleB = {
  state: {
    city:'',
    change1: false,
    id:'',
    count1: 4,
    ids: 0,
    sizer: [],
    kind: 0,
    bull: false,
    navleft: 'ele.me',
    iscut: false,
    retreat: false,
    isfoot:true,
    ishead:true,
    foods:'',
    bull2:false,
    history: [],
    cityid:0,
    userId:'',
    active: [],
    isshow3:false,
    shoplist:[],
    distribution:1,
    support:1,
    restaurant:236,
    isbuyCar: true,
    isList:false,
    shopCarList:[],
    shopB:false,
    sendUp:'还差20元起送',
    ids1:[],
    totalarr:[],
    totalPrice: 0,
    allcount:0,
    fooditem:{},
    deleFcount:0
  },
  getters: {
  },
  mutations: {
    //储存页面的函数
    getTotal(state, totalarr){
      state.totalarr = totalarr
    },
    //更新购物车的函数
    updateshopList(state){
      //处理页面退出再进入清空购物车页面不更新的问题
      state.sendUp = '还差20元起送'
      state.totalarr.forEach(value=>{
        Vue.set(value,'count',value.count=0)
        value.foods.forEach(item1=>{
          if (item1.count!==0){
            Vue.set(item1,'count',item1.count=0)
            item1.specfoods.forEach(item2=>{
              if(item2.count!==0){
                Vue.set(item2,'count',item2.count=0)
              }
            })
          }
        })
      })
    },
    //更新ids1的函数
    updateids1(state,ids1){
      state.ids1 = ids1
    },
    //处理购物车颜色
    changshopB(state){
      state.shopB = true
      state.sendUp = '去结算'
    },
    changshopC(state){
      state.shopB = false
      state.sendUp = '还差20元起送'
    },
    //处理普通添加购物车的函数
    addshop(state,arr){
      arr[1].count++
      arr[1].specfoods[arr[0]].count++
      state.shopB = true
      state.sendUp = '去结算'
    },
    //处理减少购物车的函数
    deleteS(state,arr){
      arr[1].count--
      arr[1].specfoods[arr[0]].count--
    },
    //处理在购物车添加的逻辑
    addshop1(state,item){
      state.shopB = true
      state.sendUp = '去结算'
      state.totalarr.forEach(item1=>{
        item1.foods.forEach(item2=>{
          item2.specfoods.forEach(item3=>{
            if(item3.food_id===item.food_id){
              item3.count++
              item2.count++
            }
          })
        })
      })
    },
    deleteS1(state,item){
      state.totalarr.forEach(item1=>{
        item1.foods.forEach(item2=>{
          item2.specfoods.forEach(item3=>{
            if(item3.food_id===item.food_id){
              item3.count--
              item2.count--
            }
          })
        })
      })
    },
    support(state, support){
      state.support = support
    },
    restaurant(state, restaurant){
      state.restaurant = restaurant
    },
    //是否结束过渡动画
    transitionend(state, isshow3){
      state.isshow3 = isshow3
    },
    //活动属性
    getActive(state, active){
      state.active = active
    },
    getHistory(state,history){
      state.history = history
    },
    //是否显示头部
    ishead(state,ishead) {
      state.ishead = ishead
    },
    //是否显示尾部
    isfoot(state, isfoot){
      state.isfoot = isfoot
    },
    //是否显示个人中心图标
    ispron(state, bull){
      state.bull = bull
    },
    isretreat(state, retreat){
      state.retreat = retreat
    },
    sizerFood(state, sizer){
      state.sizer = [...sizer]
    },
    changecity1(state,pl1){
      state.foods= pl1.name1;
      state.bull2 = pl1.bull
    },
    //head中间的部分
    changeCity(state,pl) {
      state.city=pl.name;
      state.change1=pl.bul
    },
    changeId(state,id){
      state.id=id
    },
    getskip(state,ids){
      state.ids = ids
    },
    getcount(state,index){
      state.count1 =  index
    },
    getFoodKind(state,index){
      state.kind = index
    },
    isEle(state, navleft){
      state.navleft = navleft
    },
    isCut(state, iscut){
      state.iscut = iscut
    },
    getcityid(state,cityid){
      state.cityid = cityid
    },
    userId(state,id){
      state.userId=id
    },
    distribution(state, distribution){
      state.distribution = distribution
    },
    isbuyCar(state, bull){
      state.isbuyCar = bull
    },
    isList(state, isList){
      state.isList = isList
    }
  },
  actions: {}
}
const moudleC = {
  state: {
    longitude: 0,
    latitude: 0,
    geohash:''
  },
  getters: {
  },
  mutations: {
    getgeohash(state,geohash){
      state.geohash = geohash
    },
    getLocation(state, location){
      state.longitude= location.longitude
      state.latitude = location.latitude
    }
  },
  actions: {}
}
const store = new vuex.Store({
  modules: {
    wang : moudleA,
    zhang: moudleB,
    hou: moudleC,
    isShow: true,
    isShow1: true
  },
  mutations: {
    //自己写函数改变自己需要改变的变量,state固定写法,play代表需要改变的变量值
    changeNav(state, play){
      state.isShow = play
    }
  },
  actions: {
    //自己写函数,调用自己写的mutations里面的函数,掉哪个函数,写哪个函数名,第二个参数表示要传的数据
    changeNav1(content, play1){
      console.log(play1)
      content.commit('changeNav', play1)
    }
  }
})
export default store



