<template>
  <div class="wrap">
    <div v-if="many" class="many" @click="many=!many"><p>多规格产品请在购物车删除</p></div>
    <!--弹窗的页面-->
    <ul class="alterul" v-if="isalter">
      <li>
        <p>
          <span>{{alterArr[0].name}}</span>
          <span class="pull-right deletealter"  @click="isalter=!isalter">×</span>
        </p>
        <p class="guige">规格</p>
        <p class="size">
          <span :class="isblue===index?'sizenext':''" v-for="(complete,index) in alterArr" :key="index" @click="isblue=index">{{complete.specs_name}}</span>
        </p>
        <p class="add1">
          <span class="price pull-left">￥{{alterArr[0].price}}</span>
          <button class="btn btn-info pull-right" @click="addCar1(foods)">加入购物车</button>
        </p>
      </li>
    </ul>
    <div v-if="isalter" @click="isalter=!isalter" class="alter">
    </div>
    <div class="box">
      <ul class="left" ref="l_list">
        <li @click="change(index)" ref="l_item" :class="{'act':index === actli}"
            v-for="(item,index) in arr" :key="index" style="position: relative">
          <p>
            {{item.name}}
          </p>
          <div class="redball" v-if="arr[index].foods.totalCount1">{{arr[index].foods.totalCount1}}</div>
        </li>
      </ul>
      <section class="r_list" ref="r_list">
        <div class="new">
          <div v-for="(it,index) in arr" :key="index" ref="product">
            <p class="title">{{it.name}}</p>
            <ul>
              <li v-for="(item,index) in it.foods" :key="index" class="list ">
                <div class="list_item flex" :class="index===it.foods.length-1?'last':''">
                  <p class="picture"><img :src="http+item.image_path" alt=""></p>
                  <div class="show">
                    <p class="name">{{item.name}}</p>
                    <p class="des">{{item.description}}</p>
                    <p class="sale">{{item.tips}}</p>
                    <p class="price">￥{{item.specfoods[0].price}}</p>
                    <div class="rightF">
                      <transition name="minus">
                        <p class="minus" v-if="item.count" @click="deleteS(item,item._id,it.foods)">-</p>
                      </transition>
                      <p class="cases" v-if="item.count">{{item.count}}</p>
                      <!--点击规格传规格对象-->
                      <p v-if="item.specfoods[0].specs[0]"class="specification" @click="alter1(item,item.specfoods,it.foods)">选规格</p>
                      <p v-else class="add" @click="addCar(item,$event,it.foods)">+</p>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </section>
    </div>
    <buy-cart ref="chlid" @changeminus="changeminus"></buy-cart>
    <div id="ball" ref="boll">+</div>
  </div>
</template>
<script>
  import Better from 'better-scroll'
  import Vue from 'vue'
  import BuyCart from "../../../components/common/buyCart";
  export default {
    name: "foodDetail",
    components: {BuyCart},
    data(){
      return {
        arr:[],
        actli:0,
        http:'http://elm.cangdu.org/img/',
        set:[0],
        left:'',
        rgt:'',
        flag:true,
        scrollY: 0,
        alterArr:[],
        isalter:false,
        fooditem:{},
        isblue:0,
        minus:false,
        cases:1,
        many:false,
        totalCount1:0,
        foods:[]
      }
    },
    created(){
      this.$store.commit('isbuyCar', true)
      var that=this
      if(localStorage.getItem(`${that.$store.state.zhang.id}`)===null){
        Vue.axios.get('https://elm.cangdu.org/shopping/v2/menu?restaurant_id='+that.$store.state.zhang.id,null).then((response)=>{
          that.arr=response.data
          that.arr.forEach(item=>{
            item.foods.forEach(item1=>{
              Vue.set(item1,'count',0)
              item1.specfoods.forEach(item2=>{
                Vue.set(item2,'count',0)
              })
            })
          })
          that.$store.commit('getTotal',that.arr)
          this.$nextTick(()=>{
            that.left = new Better(that.$refs.l_list,{click:true})
            that.rgt = new Better(that.$refs.r_list,{click: true, probeType: 3})
            that.$refs.product.forEach((el, index)=>{
              that.set.push(el.offsetHeight+that.set[index])
            })
            that.rgt.on('scroll',(res)=>{
              if(that.flag){
                that.scrollY = Math.abs(res.y)
              }
              for(let i = 0; i < that.set.length; i++){
                if(that.scrollY > that.set[i] && that.scrollY < that.set[i + 1]){
                  that.actli = i
                  if(i === that.$refs.l_item.length - 2){
                    that.left.scrollToElement(that.$refs.l_item[1], 100, 0, 0)
                  }
                  if(i===2){
                    that.left.scrollToElement(that.$refs.l_item[0], 100, 0, 0)
                  }
                }
              }
            })
          })
        })
      }else{
        this.arr =JSON.parse(localStorage.getItem(`${that.$store.state.zhang.id}`))
        this.$store.commit('getTotal',this.arr)
        this.$nextTick(()=>{
          that.left = new Better(that.$refs.l_list,{click:true})
          that.rgt = new Better(that.$refs.r_list,{click: true, probeType: 3})
          that.$refs.product.forEach((el, index)=>{
            that.set.push(el.offsetHeight+that.set[index])
          })
          that.rgt.on('scroll',(res)=>{
            if(that.flag){
              that.scrollY = Math.abs(res.y)
            }
            for(let i = 0; i < that.set.length; i++){
              if(that.scrollY > that.set[i] && that.scrollY < that.set[i + 1]){
                that.actli = i
                if(i === that.$refs.l_item.length - 2){
                  that.left.scrollToElement(that.$refs.l_item[1], 100, 0, 0)
                }
                if(i===2){
                  that.left.scrollToElement(that.$refs.l_item[0], 100, 0, 0)
                }
              }
            }
          })
        })
      }

    },
    mounted(){
      this.arr =JSON.parse(localStorage.getItem(`${this.$store.state.zhang.id}`))
      this.$store.commit('getTotal',this.arr)
    },
    methods:{
      changeminus(res){
        this.minus = res
      },
      change(index){
        this.actli=index;
        this.flag = false;
        this.rgt.scrollToElement(this.$refs.product[index], 100, 0, 0)
        setTimeout(()=>{
          this.flag = true
        },100)
      },
      //普通的加入购物车
      addCar(i,even,k){
        this.totalCount1 = 0
        Vue.set(k,'totalCount1',this.totalCount1)
        this.minus = true
        this.$store.commit('addshop',[0,i])
        this.$refs.chlid.totalprice()
        this.$refs.boll.style.top = even.pageY+'px';
        this.$refs.boll.style.left = even.pageX+'px';
        this.$refs.boll.style.transition = 'left 0s, top 0s';
        let that = this
        setTimeout(()=>{
          that.$refs.boll.style.top = window.innerHeight+'px';
          that.$refs.boll.style.left = '0rem';
          that.$refs.boll.style.transition = 'left .3s linear, top .3s ease-in';
        }, 20)
        k.forEach(item=>{
          this.totalCount1 +=item.count
        })
        Vue.set(k,'totalCount1',this.totalCount1)
      },
      //处理左边小红球的函数
      redboll(k){
        this.totalCount1 = 0
        Vue.set(k,'totalCount1',this.totalCount1)
        k.forEach(item=>{
          this.totalCount1 +=item.count
        })
        Vue.set(k,'totalCount1',this.totalCount1)
      },
      //规格购物车
      addCar1(k){
        this.minus = true
        this.$store.commit('addshop',[this.isblue,this.fooditem])
        this.$refs.chlid.totalprice()
        this.totalCount1 = 0
        Vue.set(k,'totalCount1',this.totalCount1)
        k.forEach(item=>{
          this.totalCount1 +=item.count
        })
        Vue.set(k,'totalCount1',this.totalCount1)
      },
      alter1(item,i,k){
        this.alterArr= i
        this.fooditem = item
        this.foods = k
        console.log(item)
        this.isalter = !this.isalter
      },
      //减数的函数
      deleteS(i,m,k){
        if(i.specfoods.length===1){
          this.$store.commit('deleteS',[0,i])
        }else{
          this.many = true
        }
        this.$refs.chlid.totalprice()
        this.redboll(k);
      },
    },
    beforeRouteLeave(to,from,next){
      localStorage.setItem(`${this.$store.state.zhang.id}`,JSON.stringify(this.arr))
      next()
    },
    computed:{
      shoplist(){
        return this.$store.state.zhang.totalarr
      },
      allcount(){
        return this.$store.state.zhang.allcount
      }
    },
    watch:{

    }
  }
</script>

<style scoped>
  #ball {
    width:0.2rem;
    height:0.2rem;
    background: blue;
    opacity: 0.5;
    border-radius: 50%;
    position: fixed;
    transition: left .3s linear, top .3s ease-in;
  }
  .specical{
    width: 30%;
    height: 0.3rem;
  }
  .specification{
    width: 0.5rem !important;
    background-color: #3190e8;
    color: white;
    font-size: 0.16rem;
    border-radius: 0.05rem;
    line-height: 0.2rem;
    text-align: center !important;
    float: right;
  }
  .price{
    color: red;
  }
  #p1{
    width: 100%;
    height: 0.3rem;
    font-size: 0.2rem;
    background-color: #3190e8;
  }
  .wrap{
    width: 200% !important;
    padding-top: 0.2rem;
  }
  .box{
    width: 100%;
    display: flex;
  }
  .left{
    width: 30%;
    overflow: auto;
    height: 4.5rem;
  }
  .left::-webkit-scrollbar{
    display: none;
  }
  .left>li{
    height: 0.5rem;
    line-height: 0.5519rem;
    border-bottom: 0.01rem solid gray;
  }
  .left>li>p{
    width: 100%;
    height: 0.3rem;
    font-size: 0.16rem;
    text-align: center;
    line-height: 0.5519rem;
    text-overflow: ellipsis;
    word-break:keep-all;
  }
  .r_list{
    width: 70%;
    overflow: auto;
    height: 4.5rem;
  }
  .new>div,.new{
    width: 100%;
  }
  .title{
    width: 100%;
    height: 0.3rem;
    font-size: 0.2rem;
    text-align: left;
    background-color:#e4e4e4;
    text-overflow: ellipsis;
    word-break: break-word;
    overflow: hidden;
  }
  ul,li{
    width: 100%;
  }
  .flex{
    width: 100%;
    display: flex;
    position: relative;
  }
  .picture{
    width: 20%;
    height: 20%;
  }
  .picture>img{
    width: 100%;
    height: 100%;
  }
  .show{
    width: 70%;
    margin-left: 0.1rem;
  }
  .show>p{
    width: 100%;
    height: 0.2rem;
    font-size: 0.12rem;
    margin-top: 0.05rem;
    text-align: left;
  }
  .show>p:nth-child(1){
    text-overflow: ellipsis;
    word-break: break-word;
    overflow: hidden;
    font-size: 0.2rem;
    font-weight: 900;
  }
  .act{
    background-color: deepskyblue;
  }
  .last{
    border-bottom: none;
  }
  .a1{
    width: 0.15rem;
  }
  .add{
    width: 0.2rem !important;
    height: 0.2rem !important;
    background-color: blue;
    color: white;
    text-align: center !important;
    line-height: 0.16rem;
    border-radius: 50%;
    font-size: 0.2rem !important;
    display: inline-block;
    position: absolute;
    right: -0.1rem;
    transition: left 1s linear, top 1s ease-in;
  }
  .alter{
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.5;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
  }
  .alterul{
    height: 1.5rem;
    width:2.5rem ;
    position: absolute;
    background-color: white;
    left: 15%;
    top: 40%;
    opacity: 1 !important;
    z-index: 100;
    font-size: 0.16rem;
  }
  .alterul>li>p{
    margin: 0.1rem 0 0.1rem 0.05rem;
  }
  .guige{
    text-align: left;
  }
  .size{
    text-align: left;
  }
  .size>span{
    display: inline-block;
    width: 0.5rem;
    border: 0.01rem solid black;
    text-align: center;
    height: 0.3rem;
    line-height: 0.3rem;
    border-radius: 0.1rem;
  }
  .size>.sizenext{
    color: blue;
    border-color: blue ;
  }
  .add1{
    line-height: 0.5rem;
  }
  .btn{
    margin-right: 0.2rem;
  }
  .deletealter{
    font-size: 0.3rem;
    position: absolute;
    right: 0.05rem;
    top:0
  }
  .minus{
    width: 0.2rem !important;
    height: 0.2rem !important;
    position: absolute;
    background-color: white;
    border: 0.01rem solid black;
    color: black;
    left: 0;
    text-align: center !important;
    line-height: 0.16rem;
    border-radius: 50%;
    font-size: 0.2rem !important;
    display: inline-block;
    top:0rem
  }
  .minus-enter,.minus-leave-to{
    left:2.2rem;
    opacity: 0;
  }
  .minus-enter-active,.minus-leave-active{
    transition: all 0.5s ease;
  }
  .minus-leave,.-minus-enter-to{
    left: 1.8rem;
    opacity: 1;
  }
  .cases{
    font-size: 0.2rem !important;
    text-align: center;
    display: inline-block;
    margin-left: 0.2rem;
    position: relative;
    top: -0.1rem;
    height: 0.2rem;
  }
  .rightF{
    float: right;
    position: relative;
    max-width: 0.9rem;
    min-width: 0.7rem;
    height: 0.3rem;
  }
  .many{
    position: absolute;
    top:0;
    left:0;
    font-size: 0.16rem;
    color: white;
    height: 100%;
    width: 100%;
    z-index: 100000;
  }
  .many>p{
    position: absolute;
    top: 50%;
    left: 20%;
    background-color: black;
    height: 0.3rem;
    border-radius: 0.05rem;
    line-height: 0.3rem;
  }
  .name{
    text-overflow: ellipsis;
    word-break: keep-all;
    overflow: hidden;
  }
  .redball{
    width: 0.16rem !important;
    height: 0.16rem !important;
    background-color: red;
    border-radius: 50%;
    position: absolute;
    top:0%;
    right: 0%;
    color: white;
    line-height: 0.16rem;
    text-align: center;
    z-index: 5;
    font-size: 0.12rem;
  }
</style>



